import Vue from 'vue'
import Router from 'vue-router'
import QuestionForm from '@/components/questionForm'
import Gantt from '@/pages/gantt'
import Calendar from '@/pages/calendar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'QuestionForm',
      component: QuestionForm
    },
    {
      path: '/gantt',
      name: 'Gantt',
      component: Gantt
    },
    {
      path: '/calendar',
      name: 'calendar',
      component: Calendar
    }
  ]
})



